:root{
    --blackPieceColor: #000;
    --whitePieceColor: #fff;
    --ValidMoveColor: #E88D17;
    --SelectedPieceColor: yellow;
    --darkSquareColor: #769656;
    --lightSquareColor: #EEEED2;
    --backgroundColor: #262522;
  }
.game{
    width: 100%;
    min-height: 100vh;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    font-family: 'Courier New', Courier, monospace;
    background-color: var(--backgroundColor);
}
.App{
    width: 100%;
    min-height: 100vh;
    background-color: var(--backgroundColor);
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
}
.container{
    width: auto;
    height: 60%;
}
button{
    border: none;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 600;
    color: #f0d9b5;
    cursor: pointer;
    text-transform: capitalize;
    background-color: var(--darkSquareColor);
    min-height: 25px;
    padding: 7px;
    border-radius: 8px;
    min-width: 70px;
    flex: 1;
  }
.inner-container{
    display: flex;
    flex-direction: column;
    gap: 30px;
}

