.dialog{
    width: auto;
    height: 80%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 25px;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background-color: #44433f;
}
.dialog nav{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.dialog span{
color: var(--lightSquareColor);
text-transform: capitalize;
font-family: 'Courier New', Courier, monospace;
font-weight: 600;
font-size: 2.1rem;

}