

/*utilities styles*/
nav{
  min-height: 30px;
  padding: 10px;
  background-color: #fff;
  margin-bottom: 10px;
}

.game-menu{
  font-weight: 600;
  font-size: 1.1rem;
  text-transform: capitalize;
  display: flex;
   flex-direction: column;
  gap: 20px;
  background-color: transparent;
  justify-content: space-between;
  font-family: 'Courier New', Courier, monospace;
}
.game-menu .currentPlayer{
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  display: flex;
  gap: 10px;
  align-items: center;
  color: #f0d9b5;
  text-transform: capitalize;
  background-color: var(--darkSquareColor);
  padding: 7px;
  border-radius: 8px;
}
.game-menu .piece{
  width: 20px;
  height: 20px;
  border-radius: 50%;

}
.game-menu-items{
  display: flex;
  gap: 10px;
  background-color: transparent;
  align-items: center;
  flex-wrap: wrap;
}
nav{
  background-color: transparent;
}

.steak{
  border: none;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  position: absolute;
  top: calc(50% - 30px / 2);
  left: calc(50% - 120px / 2);
  animation-name: fadeOut;
  animation-duration: 3s;
  color: #000000;
  text-transform: capitalize;
  background-color: rgb(255, 217, 0);
  min-height: 25px;
  width: 120px;
  padding: 7px;
   opacity: 0;
  border-radius: 8px;
}
.game-over{
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  position: absolute;
  width: 30%;
  min-height: 160px;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  background-color: var(--backgroundColor);
  color: 000;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
  padding: 5px;
  border-radius: 8px;
  z-index: 999;
}
.overlayer{
  width: 100%;
  height: 100%;
  background-color: #00000070;
  position: absolute;
  inset: 0;
  z-index: 1;
  display: flex;
}
.game-over span{
  text-transform: capitalize;
  color: var(--lightSquareColor);

}
.highlight{
position: relative;
width: 100%;
height: 100%;
animation-name: highLighting;
animation-duration: 4s;
transition: all 0.5s ease-in-out;
animation-iteration-count: infinite;
shape-rendering: crispEdges;
border: 3px solid var(--ValidMoveColor);
}
.game-over button{
  flex: 0;
  width: 80%;
}
.steak-container{
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 1;
}
/* board styles */
.board {
    display: grid;
    grid-template-rows: repeat(8, auto);
    position: relative;
  }

  .row {
    display: flex;
  }
  
  .square {
    width: 50px;
    aspect-ratio: 1/1;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  .dark {
    background-color: var(--darkSquareColor);
  }
  
  .light {
    background-color: var(--lightSquareColor);
  }
  
  .piece {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: center center;
    image-rendering: smooth;
    position: relative;
  }

  .piece.b {

  background-color: var(--blackPieceColor);
 
  }
  .piece.w{
  background-color: var(--whitePieceColor);

  }
  .piece.bk{

    background-color: var(--blackPieceColor);
    fill: #fff;
    background-image: url('./assets/whiteCrown.svg');
    
  }
  .piece.wk{

    background-color: var(--whitePieceColor);
    background-image: url('./assets/blackCrown.svg');
    fill: #000;

  }

  .square.valid-move {
    background-color: var(--ValidMoveColor); 
  }
  
  .square.selected-piece {
   background-color: var(--SelectedPieceColor);
  }
  @keyframes fadeOut {
    0%{
     opacity: 1;
    }
    100%{
     opacity: 0;
    }
  }
  @keyframes highLighting {
    0% {
      transform: scale(1);
  }
  50% {
      transform: scale(1.1);
  }
  100% {
      transform: scale(1);
  }
  }
  @keyframes flipToKing {
    from { -webkit-transform: rotateY(0); -moz-transform: rotateY(0); transform: rotateY(0); }
    to { -webkit-transform: rotateY(1800deg); -moz-transform: rotateY(1800deg); transform: rotateY(1800deg);
     }
    }