.history-container ul li{
    color: #f0d9b5;
    font-weight: bolder;
    background-color: var(--darkSquareColor);
    list-style-type: decimal;
    border-radius: 8px;
    padding: 3px;
}
.history-container ul{
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-height: 200px;
    overflow-y: scroll;
}
.history-container ul{
    margin: 0;
    padding: 0;
}