.dialog{
    width: 50%;
    height: 80%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    justify-content: flex-start;
    align-items: normal;
    border-radius: 8px;
    background-color: #44433f;
    max-width: 400px;
}
.dialog form{
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.dialog nav{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.form-row{
    display: flex;
    gap: 10px;

}
.customazition-content{
display: flex;
flex-direction: column;
gap: 20px;
}
.dialog form label{
    color: var(--lightSquareColor);
    text-transform: capitalize;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bolder;
}
.form-column{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.form-row div{
    display: flex;
    color: var(--lightSquareColor);
text-transform: capitalize;
font-family: 'Courier New', Courier, monospace;
}
.form-row .selected{
    background-color: var(--ValidMoveColor);

}
.dialog span{
color: var(--lightSquareColor);
text-transform: capitalize;
font-family: 'Courier New', Courier, monospace;
font-weight: 600;
font-size: 2.1rem;

}